import { DragDropContext, Droppable, Draggable, DropResult } from "react-beautiful-dnd";
import { useRecoilState } from "recoil";
import styled from "styled-components";
import { toDoState } from "./atoms";
import Board from "./Components/Board";


const Wrapper = styled.div`
  display: flex;
  width: 100vw;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  height: 100vh;
`;

const Boards = styled.div`
  display: grid;
  width: 100%;
  gap: 10px;
  grid-template-columns: repeat(3, 1fr);
  display: flex;
  justify-content: center;
  align-items: flex-start;
`;



function App() {
  const [toDos, setToDos] = useRecoilState(toDoState)
  const onDragEnd = (info: DropResult) => { // drag    
    const { destination, draggableId, source } = info;
    if (!destination) return;
    if (destination?.droppableId === source.droppableId) { //  board  
      setToDos(allBoards => {
        const boardCopy = [...allBoards[source.droppableId]];
        const taskObj = boardCopy[source.index]; //      ({id: number, text: string}  
        boardCopy.splice(source.index, 1);
        boardCopy.splice(destination?.index, 0, taskObj) //      
        return {
          ...allBoards, // (1)  
          [source.droppableId]: boardCopy, // (2)   :  boardCopy 
        } // preToDos  boardcopy()     
      });
    }
    if (destination?.droppableId !== source.droppableId) { //  baord 
      setToDos((allBoards) => {
        const sourceBoard = [...allBoards[source.droppableId]];
        const taskObj = sourceBoard[source.index];
        const destinationBoard = [...allBoards[destination.droppableId]];
        sourceBoard.splice(source.index, 1);
        destinationBoard.splice(destination.index, 0, taskObj);
        return {
          ...allBoards,
          [source.droppableId]: sourceBoard,
          [destination.droppableId]: destinationBoard,
        }
      })
    }

    /*     setToDos(preToDo => {
          const copyToDo = [...preToDo];
          copyToDo.splice(source.index, 1);
          // 1) source.index  1  (  )
          copyToDo.splice(destination?.index, 0, draggableId)
          // 2) destination.index  draggableId(toDo ) (    )
          return copyToDo
        }); */
  };
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Wrapper>
        <Boards>
          {Object.keys(toDos).map((boardId) => <Board key={boardId} boardId={boardId} toDos={toDos[boardId]} />)}
          {/* Board  toDos toDosState   boardId(to do, doing, done) . Board property toDos Baord   */}
        </Boards>
      </Wrapper>
    </DragDropContext>
  );
}

export default App;
