import { Droppable } from "react-beautiful-dnd";
import { useForm } from "react-hook-form";
import { useSetRecoilState } from "recoil";
import styled from "styled-components";
import { IToDo, toDoState } from "../atoms";
import DraggableCard from "./DraggableCard";

interface IAreaProps {
  isDraggingOver: boolean;
  isDraggingFromThis: boolean;
}

const Wrapper = styled.div`
width: 300px;
padding-top: 10px;
 background-color: ${(props) => props.theme.boardColor};
border-radius: 5px;
min-height: 300px;
display: flex;
flex-direction: column;
`;

const Title = styled.h2`
  text-align: center;
  font-weight: 600;
  margin-bottom: 10px;
  font-size: 18px;
`;

const Area = styled.div<IAreaProps>`
background-color: ${props => props.isDraggingOver
    ? "#b2bec3"
    : props.isDraggingFromThis
      ? "#dfe6e9"
      : "transparent"};
flex-grow: 1;
transition: background-color 0.3s ease-in-out;
padding: 20px;
`;
const Form = styled.form`
width: 100%;
input {
  width: 100%;
}
`;


interface IBoardProps {
  toDos: IToDo[]; //  toDos  interface App string[] 
  boardId: string;
};
interface IForm {
  toDo: string;
};

function Board({ toDos, boardId }: IBoardProps) {
  const setToDos = useSetRecoilState(toDoState);
  const { register, setValue, handleSubmit } = useForm<IForm>();
  const onValid = ({ toDo }: IForm) => { // data(form     toDo(input register  ))
    const newToDo = {
      id: Date.now(),
      text: toDo,
    };
    setToDos(allBoards => {
      return {
        ...allBoards,
        [boardId]:[
          newToDo, ...allBoards[boardId] // newToDo:  , allBoards[boardId]:  boardId    
        ]
      }
    })
    setValue("toDo", "");
  }
  return (
    <Wrapper>
      <Title>{boardId}</Title>
      <Form onSubmit={handleSubmit(onValid)}>
        <input {...register("toDo", { required: true })} type="text" placeholder={`Add a task on ${boardId}`}>
        </input>
      </Form>
      <Droppable droppableId={boardId}>
        {(provided, snapshot) => (
          <Area isDraggingOver={snapshot.isDraggingOver} isDraggingFromThis={Boolean(snapshot.draggingFromThisWith)} ref={provided.innerRef} {...provided.droppableProps}>
            {toDos.map((toDo, index) => (
              <DraggableCard key={toDo.id} toDoId={toDo.id} toDoText={toDo.text} index={index} />
            ))}
            {provided.placeholder}
            {/*      x */}
          </Area>
        )}
      </Droppable>
    </Wrapper>
  );
}

export default Board;