import { atom } from "recoil";

export interface IToDo {
  id: number;
  text: string;
}
interface IToDoState {
  [key: string]: IToDo[]; //   boardId key IToDo  
}


export const toDoState = atom<IToDoState>({
  key: "toDo",
  default: { //  Board  boardId (to_do, doing, done)
    "To Do": [], // string[] => string  array
    Doing: [], // IToDoState   3 property(to_do, doing, done) string   
    Done: []
  },
});